import { Injectable } from '@angular/core';
import { APIService } from '../services/API.service';
export interface ContentsType {
  tartalom:string;
  index:string;
  id:number;
}

@Injectable()
export class HtmlcontentsService {
  public kapcsolat_cim:string = '';
  public contents:any =
  {
    kapcsolat_tartalom: "",
    kapcsolat_cim: "",
    regisztracio_mielott: "",
    adataim_balbox: ""
  }


    ;

  constructor(private _APIService:APIService) {
    this.loadsAllContents();
  }


  public setContent(id, content) {


    this._APIService.getResponsePUT('api/setcontents/' + id, {tartalom: content}).subscribe(
        data=> {
          //console.log(data);
        this.loadsAllContents();
      },
        error=> {
        console.log(error);
      })
  }


  private loadsAllContents() {
    this._APIService.getResponseGET('api/getcontents/', '').subscribe(
        data=> {

        this.contents = data;


      },
        error=> {
        console.log(error);
      })
  }
}
